<template>
  <v-form ref="form" @submit.prevent>
    <v-container>
      <v-row align="center" justify="center">
        <v-subheader class="text-center">Solicitar Cesta Básica</v-subheader>
      </v-row>
      <v-row align-center hidden>
        <v-col class="d-flex" cols="6">
          <v-text-field v-model="solicitacao.cdSolicitacao" label="Id" outlined disabled></v-text-field>
        </v-col>
      </v-row>
      <v-row align="center" justify="center">
        <v-col class="d-flex" cols="6">
          <v-combobox
            :items="beneficiados"
            :hint="`${'Número de Pessoas na Família:' + solicitacao.beneficiado.npf}`"
            persistent-hint
            v-model="solicitacao.beneficiado"
            item-text="nome"
            item-value="cdUsuario"
            label="Beneficiado"
            placeholder="Escolha o Beneficiado"
            return-object
            outlined
            required></v-combobox>
        </v-col>
      </v-row>
      <v-row align="center" justify="center">
        <v-col class="d-flex" cols="6">
          <v-combobox
          :items="instituicoes"
          :hint="`${formatedHint}`"
          persistent-hint
          v-model="solicitacao.instituicao"
          item-text="nome"
          item-value="cdUsuario"
          label="Instituição"
          placeholder="Escolha a Instituição"
          return-object
          outlined
          required></v-combobox>
        </v-col>
      </v-row>
      <v-row align="center" justify="center">
        <v-col class="d-flex" cols="6">
          <v-combobox
            :items="tpCestas"
            :hint="`${solicitacao.tpCesta.descricao}`"
            persistent-hint
            v-model="solicitacao.tpCesta"
            item-text="nome"
            item-value="id"
            label="Tipo de Cesta"
            placeholder="Escolha o Tipo de Cesta"
            return-object
            outlined
            required></v-combobox>
        </v-col>
      </v-row>
      <v-row align="center" justify="center">
        <v-col class="d-flex" cols="6" >
            <v-slider
              v-model="solicitacao.qtdCestas"
              type="number"
              min=1
              max=50
              :value="`${solicitacao.qtdCestas}`"
              thumb-label="always"
              label="Qtd. Cestas"
              outlined
              required></v-slider>
          </v-col>
        </v-row>
        <v-row align="center" justify="end">
          <v-col class="d-flex" cols="6" >
            <v-btn color="success" class="" @click="save">Salvar</v-btn>
          </v-col>
        </v-row>
    </v-container>
  </v-form>
</template>

<script>
import DataService from "../service/SolicitacaoDataService";
import BeneficiadoDataService from "../service/BeneficiadoDataService";
import InstituicaoDataService from "../service/InstituicaoDataService";
import TipoCestaDataService from "../service/TipoCestaDataService";

export default {
  data: () => ({
    search: '',
    beneficiados: [],
    instituicoes: [],
    tpCestas: [],
    solicitacao: {cdSolicitacao:0, dtSolicitacao:'', qtdCestas:0, status:0, tpCesta:{id:0,nome:'',descricao:''}, instituicao: {cdUsuario:0,nome:'',login:'',senha:'',cnpj:0,rua:'',numImovel:'',bairro:{id:0,nome:'',cidade:{id:0,nome:'',uf:{id:0,sigla:'',nome:''}}}}, beneficiado:{cdUsuario:0}},
  }),
  computed: {
    id() {
      return this.$route.params.id;
    },
    now() {
      return new Date().toISOString().substr(0, 10)
    },
    formatedHint() {
      return this.solicitacao.instituicao.bairro.nome + ' - ' + this.solicitacao.instituicao.bairro.cidade.nome + ' - ' + this.solicitacao.instituicao.bairro.cidade.uf.nome
    }
  },
  watch: {

  },
  methods: {
    initialize() {
      if(this.id > 0){
        DataService.retrieve(this.id).then(response => {
          this.solicitacao = response.data
        });
      }
      BeneficiadoDataService.retrieveAll().then(response => {
        this.beneficiados = response.data
      });
      InstituicaoDataService.retrieveAll().then(response => {
        this.instituicoes = response.data
      });
      TipoCestaDataService.retrieveAll().then(response => {
        this.tpCestas = response.data
      });
    },
    save(e) {
      e.preventDefault();
      if(this.id > 0){
        DataService.update(this.id, this.solicitacao, {
          id: this.id,
          solicitacao: this.solicitacao
        }).then(() => {
          this.$router.push('/solicitacoes')
        }).catch(e => {
          console.log(e)
          if(e.response){
            this.$store.dispatch('setSnackbar', {
              showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
            });
          }
        });
      } else {
        this.solicitacao.dtSolicitacao = this.now
        this.solicitacao.status = 0
        DataService.create(this.solicitacao, {
          solicitacao: this.solicitacao
        }).then(() => {
          this.$router.push('/solicitacoes')
        }).catch(e => {
          console.log(e)
          if(e.response){
            this.$store.dispatch('setSnackbar', {
              showing: true, text: e.response.data.message, timeout: 8000, color: 'error'
            });
          }
        });
      }
    }
  },
  created() {
    this.initialize()
  }
};
</script>

